import styles from './table.module.scss'
import React from 'react'

export interface TableColumn {
    readonly id: string
    readonly title: string | JSX.Element
    readonly itemProp?: string
}

export interface TableData {
    [columnId: string]: string | JSX.Element | undefined
}

export interface TableProps {
    readonly columns: TableColumn[]
    readonly rows: TableData[]
    readonly headerHidden?: boolean
    readonly rowItemType?: string
    readonly rowItemProp?: string
}

function Table({ columns, rows, headerHidden, rowItemType, rowItemProp }: TableProps) {
    return (
        <div className={styles.Table}>
            <table>
                {!headerHidden && (
                    <thead>
                        <tr>
                            {columns.map(({ id, title }) => (
                                <th key={id}>{title}</th>
                            ))}
                        </tr>
                    </thead>
                )}
                <tbody>
                    {rows.map((row, idx) => (
                        <tr
                            itemType={rowItemType}
                            itemScope={!!rowItemType}
                            itemProp={rowItemProp}
                            key={idx}
                        >
                            {columns.map(({ id, itemProp }) => (
                                <td itemProp={itemProp} key={id}>
                                    {row[id]}
                                </td>
                            ))}
                        </tr>
                    ))}
                </tbody>
            </table>
        </div>
    )
}

export default Table
